.spec-panel {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 70px;
	z-index: 999;
	font-size: 16px;
	line-height: 1;
	overflow: hidden;
	box-shadow: 0 0 0 1px currentColor;
	background-color: inherit;
	will-change: transform;
	-webkit-transform: translate(0, 0);
	-ms-transform: translate(0, 0);
	transform: translate(0, 0);
	-webkit-transition: transform 0.3s linear;
	transition: transform 0.3s linear;
}

@media (max-width: 479px) {
	.spec-panel {
		height: 70px;
		width: 100%;
		border-width: 0 0 2px 0;
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(0, 0, 0, 0.3);
	}
	.schemecolor-white .spec-panel {
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(255, 255, 255, 0.3);
	}
	.schemecolor-blue .spec-panel {
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(0, 76, 146, 0.3);
	}
}

.vspec .spec-panel {
	display: block;
}

.spec-panel:hover,
.touchevents .spec-panel {
	overflow-y: auto;
}

@media (max-width: 479px) {
	.spec-panel:hover,
	.touchevents .spec-panel {
		overflow-y: hidden;
		overflow-x: auto;
	}
}

@media (max-width: 979px) {
	.nav-opened .inner-page .spec-panel {
		transform: translate(-100%, 0);
	}
}

@media (max-width: 479px) {
	.nav-opened .inner-page .spec-panel {
		transform: translate(0, calc(-100% - 2px));
		box-shadow: none;
	}
}

@media (max-width: 767px) {
	.nav-opened .main-page .spec-panel {
		transform: translate(-100%, 0);
	}
}

@media (max-width: 479px) {
	.spec-panel .sv-btn-group {
		display: inline-block;
		vertical-align: top;
	}
}

.spec-panel hr {
	border-color: currentColor;
	border-bottom-width: 1px;
	margin: 20px 0;
}

@media (max-width: 479px) {
	.spec-panel hr {
		display: inline-block;
		vertical-align: top;
		border-left: 1px solid currentColor;
		border: none;
		width: 0;
		height: 36px;
		margin: 0 10px 0 0;
	}
}

.spec-panel a {
	display: block;
	margin-bottom: 18px;
	margin-left: auto;
	margin-right: auto;
}

@media (max-width: 479px) {
	.spec-panel a {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 0;
		margin-right: 10px;
	}
}

.spec-panel_h {
	padding: 20px 0;
	text-align: center;
	letter-spacing: -4px;
	font-size: 0;
	line-height: 1;
}

@media (max-width: 479px) {
	.spec-panel_h {
		white-space: nowrap;
		padding: 10px;
	}
}

.spec-panel,
.spec-panel_h {
	width: 70px;
	box-sizing: border-box;
}

@media (max-width: 479px) {
	.spec-panel,
	.spec-panel_h {
		height: 56px;
		width: 100%;
	}
