@import "../libs/include-media/dist/include-media";
@import "utils/variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

// variables
$displace: 1px;
// variables end

.spec-panel {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 70px;
	z-index: 999;
	font-size: 16px;
	line-height: 1;
	overflow: hidden;
	box-shadow: 0 0 0 1px currentColor;
	//background-color: var(--main-bg-color);
	background-color: inherit;
	will-change: transform;
	-webkit-transform: translate(0, 0);
	-ms-transform: translate(0, 0);
	transform: translate(0, 0);
	-webkit-transition: transform 0.3s linear;
	transition: transform 0.3s linear;

	@include media('<mob-md') {
		height: 70px;
		width: 100%;
		border-width: 0 0 2px 0;
		$sh1: 0 0 0 2px;
		$sh2: 0 4px 4px;
		box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color, 0.3);

		.schemecolor-white & {
			box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color--scheme-white, 0.3); }

		.schemecolor-blue & {
			box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color--scheme-blue, 0.3); } }

	.vspec & {
		display: block; }

	&:hover,
	.touchevents & {
		overflow-y: auto;

		@include media('<mob-md') {
			overflow-y: hidden;
			overflow-x: auto; } }

	.nav-opened .inner-page & {
		@include media('<tab-lg') {
			transform: translate(-100%, 0); }

		@include media('<mob-md') {
			transform: translate(0, calc(-100% - 2px));
			box-shadow: none; } }

	.nav-opened .main-page & {
		@include media('<tab-sm') {
			transform: translate(-100%, 0); } }

	.sv-btn-group {
		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top; }

		//a:last-child
		//argin-bottom: 0

		//include media('<mob-md')
 }		//margin-right: 0

	hr {
		border-color: currentColor;
		border-bottom-width: 1px;
		margin: 20px 0;

		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top;
			border-left: 1px solid currentColor;
			border: none;
			width: 0;
			height: 36px;
			margin: 0 10px 0 0; } }

	a {
		display: block;
		margin-bottom: 18px;
		margin-left: auto;
		margin-right: auto;

		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top;
			margin-bottom: 0;
			margin-right: 10px; } } }

.spec-panel_h {
	padding: 20px 0;
	text-align: center;
	letter-spacing: -4px;
	font-size: 0;
	line-height: 1;

	@include media('<mob-md') {
		white-space: nowrap;
		padding: 10px; } }

.spec-panel,
.spec-panel_h {
	width: 70px;
	box-sizing: border-box;

	@include media('<mob-md') {
		height: 56px;
		width: 100%; } }

// button enable special version
.sv-btn-on {
	$size: 42px;

	position: fixed;
	left: 10px;
	top: 10px;
	width: $size;
	height: $size;
	font-size: 16px;
	background-color: wheat;
	box-sizing: border-box;

	span {
		font-size: 0;
		line-height: 0; }

	svg {
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		box-sizing: border-box; }

	path {
		fill: currentColor; }

	&:hover, &:focus {
		transform: translate(-$displace, -$displace);
		box-shadow: 3px 3px 3px rgba(black, 0.3);

		.header & {
			box-shadow: none;
			transform: none; } }

	&, &:active {
		transform: translate(0, 0);
		box-shadow: 2px 2px 2px rgba(black, 0.2);

		.header & {
			box-shadow: none;
			transform: none; } }

	// special version view
	.vspec & {
		background: none;

		&::before {
			content: "";
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			width: 40px;
			height: 3px;
			margin: -1px 0 0 -20px;
			border: none;
			background-color: currentColor;
			transform: rotate(-0.1turn);

			@include media('<mob-md') {
				width: 34px;
				margin: -1px 0 0 -17px; } } }

	// in header
	.header & {
		$size: 60px;
		float: right;
		position: relative;
		left: auto;
		top: auto;
		width: $size;
		height: $size;
		box-shadow: none; }

	// in footer
	.footer & {
		$size: auto;
		left: auto;
		top: auto;
		display: block;
		position: relative;
		width: $size;
		height: $size;
		padding-left: 40px;
		font-size: 16px;
		//color: inherit
		color: white;
		background: none;
		box-shadow: none;

		@include media('<mob-md') {
			float: left;
			left: 50%;
			width: 265px;
			max-width: 100%;
			text-align: left;
			vertical-align: top;
			transform: translate(-50%, 0); }

		&:hover, &:focus, &:active {
			transform: none;

			@include media('<mob-md') {
				transform: translate(-50%, 0); } }

		span {
			font-size: inherit;
			line-height: inherit; }

		svg {
			left: 0;
			transform: translate(0, -50%); }

		.vspec & {
			color: inherit;
			font-size: 20px;
			line-height: 26px;
			width: auto;
			float: none;
			left: auto;
			transform: none;

			&:before {
				content: none; } } } }

.spec-panel .sv-btn {
	$size: 42px;
	$arr: 24px;

	width: $size;
	height: $size;
	position: relative;
	font-size: 16px;
	line-height: 1;
	letter-spacing: normal;
	text-decoration: none;
	border-radius: 1px;
	//border: 3px solid
	//border-color: rgba($spec-text-color, 0.2) rgba($spec-text-color, 0.6) $spec-text-color rgba($spec-text-color, 0.4)
	border: 1px solid $spec-text-color;
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0) !important;
	user-select: none !important;

	@include media('<mob-md') {
		$size: 36px;
		width: $size;
		height: $size; }

	.schemecolor-white & {
		border-color: $spec-text-color--scheme-white; }

	.schemecolor-blue & {
		border-color: $spec-text-color--scheme-blue; }

	&::after {
		content: "";
		display: none;
		position: absolute;
		top: 50%;
		right: -22px;
		height: $arr;
		width: $arr;
		margin-top: -#{round($arr/2)};
		@include triangle("left", currentColor, round($arr/2), 1.2);

		@include media('<mob-md') {
			border: none;
			@include triangle("top", currentColor, round($arr/2), 1.2);
			top: auto;
			right: auto;
			left: 50%;
			bottom: -20px;
			margin: 0 0 0 -#{round($arr/2)}; } } }

